<template>
  <div class="product-page">
    <ProductTemplate
      v-for="product in cart"
      :key="product"
      :product="product"
    ></ProductTemplate>
  </div>
  <hr />
  <div class="checkout">
    <p>Total : {{ total2 }}</p>
    <button @click="checkout">Checkout</button>
  </div>
  <ModelOverlay
    v-show="showsuccessmodel"
    message="Order placed successfully"
    route=""
  ></ModelOverlay>
</template>
<script>
import ProductTemplate from "../components/ProductTemplate.vue";
import { mapState } from "vuex";
import ModelOverlay from "../components/ModelOverlay.vue";
export default {
  components: {
    ProductTemplate,
    ModelOverlay,
  },
  data() {
    return {
      total: 0,
      showsuccessmodel: false,
    };
  },
  computed: {
    ...mapState({ cart: "cart" }),

    total2() {
      this.cart.forEach((element) => {
        this.total = this.total + element.price;
      });
      return this.total;
    },
  },
  methods: {
    checkout() {
      this.showsuccessmodel = !this.showsuccessmodel;
    },
  },
};
</script>
<style scoped>
button {
  border: none;
  width: 100px;
  height: 30px;
  border-radius: 8px;
  color: white;
  background-color: rgb(255, 216, 20);
}
.checkout {
  float: right;
}
.product-page {
  display: flex;
  flex-wrap: wrap;
  width: 75%;
  margin: auto;
}
</style>
